var expmonscales = {};  //{}
for(var name in exps){
    var json = {};
    var zyexp = exps[name];
    var zymon = mons[name];
    for(var name2 in zyexp){
        var expnums = zyexp[name2];
        var monnums = zymon[name2];
        for(var name3 in expnums){
            var expnum = expnums[name3];
            var monnum = monnums[name3];
            if(!json[name3])json[name3] = [];
            json[name3][name2] = {
                name:name3,
                level:name2,
                exp:expnum,
                mon:monnum,
                scale:expnum/monnum
            }
        }
    }
    expmonscales[name] = json;
}

export {
    exps,mons,peos,attrs,expmonscales
}
